import { exportComponentAsJPEG, exportComponentAsPDF, exportComponentAsPNG } from "react-component-export-image";
import React from "react";
import { Theme, createStyles, makeStyles, useTheme, fade} from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import PhotoIcon from '@material-ui/icons/Photo';
import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
import { Hidden } from '@material-ui/core';
import background from '../images/report08.jpg';
import "./ReportOne.css";
export class ComponentToPrint extends React.Component {
  constructor(props) {
    super(props);
    this.myTextInput = React.createRef();
  }


 handleClickDownload = (event) => {
  window.open(event,'_blank');
}
 render() {
  var s = this.props.obj.topic;
  var a = '';
  var prefix = 'เ';
  var suffix = 'โ';
	var i = s.indexOf(prefix);
	if (i >= 0) {
		s = s.substring(i + prefix.length);
	}
	else {
		a = '';
	}
	if (suffix) {
		i = s.indexOf(suffix);
		if (i >= 0) {
			s = s.substring(0, i);
		}
		else {
		  a = '';
		}
	}
	 a = s;
  console.log(a);
  
   return <React.Fragment>
   <div
     className="page"
     style={{
       backgroundImage: `url(${background})`,
     }}
   >
    
            <LazyLoadImage className="app_picture1" alt="" effect="blur" width="100%" style={{
    objectFit: "cover",}}
       src={this.props.obj.pictures.split(',')[0].trim().replace("open", 
       "uc")} 
       onClick={()=> {this.handleClickDownload(this.props.obj.pictures.split(',')[0])}}/>
      
      {this.props.obj.topic.split(" ").map((txt) => (
        <Typography className="app_top1" 
    ref={this.myTextInput} component="h6" variant="h6">
            {txt}
          </Typography>
      ))}
          
          <Typography className="app_top2"  variant="subtitle1" color="textSecondary">
            <span>{"วันที่ ".concat(this.props.obj.day).concat("/").concat(this.props.obj.month).concat("/").concat(this.props.obj.year)}</span>
          </Typography>
          <Typography className="app_top3" variant="subtitle1" color="textSecondary">
            <span>{"รายละเอียด: ".concat(this.props.obj.detail)}</span>
          </Typography>
          <Typography variant="subtitle2" color="textSecondary">
            <span>{"ไฟล์แนบ: "}
            { this.props.obj.pictures != "" && ( this.props.obj.pictures.split(',').map((item,ind) => 
            <IconButton key={ind} aria-label="share"
             onClick={()=> {this.handleClickDownload(item)}}>
            <PhotoIcon style={{color: "#FF8A65"}}/>
            </IconButton> 
        ))}
        { this.props.obj.files != "" && (this.props.obj.files.split(',').map((item,ind) => 
            <IconButton key={ind} aria-label="share"
             onClick={()=> {this.handleClickDownload(item)}}>
            <InsertDriveFileIcon style={{color: "#e57373"}}/>
            </IconButton> 
        ))}
            </span>
          </Typography>
   </div>
   </React.Fragment>;
 }
}
export default class ReportOne extends React.Component {
 constructor(props) {
   super(props);
   this.componentRef = React.createRef();
 }

 render() {
   return (
   <React.Fragment>
       <ComponentToPrint obj={this.props.obj} index={this.props.index}  ref={this.componentRef} />
       <button onClick={() => exportComponentAsJPEG(this.componentRef)}>
           Export As JPEG
       </button>
       <button onClick={() => exportComponentAsPDF(this.componentRef)}>
           Export As PDF
       </button>
       <button onClick={() => exportComponentAsPNG(this.componentRef)}>
           Export As PNG
       </button>
   </React.Fragment>);
 }
}
