import React, {useEffect} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import { TransitionProps } from '@material-ui/core';
import Hidden from "@material-ui/core/Hidden";

import Grid from "@material-ui/core/Grid";
import { makeStyles,createStyles, useTheme } from '@material-ui/core/styles';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: theme.palette.background.paper,
      width: "98%",
    },
    gridList: {
        width: "100%",
        height: "60vh",
        overflow: "scroll"
    },
    title: {
      color: theme.palette.secondary.light,
    },
    titleBar: {
      background:
        'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
    },
  }),
);

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & { children?: React.ReactElement<any, any> },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertDialogSlide({iopen, data, setIsCert}) {
  const [open, setOpen] = React.useState(iopen);
  const classes = useStyles();
  const [selectedImage, setSelectedImage] = React.useState(0);
  const [total, setTotal] = React.useState(0);
  const [selectedData, setSelectedData] = React.useState();
  const [selectedDataChange, setSelectedDataChange] = React.useState(false);
  useEffect(() => {
    // Run! Like go get some data from an API.
    var imgs = [];
    var i = 0;
    data.map((tile,index) => {
      tile.pictures.split(',').map((pic,ind) => {
        imgs.push(pic.trim().replace("https://drive.google.com/open?id=","https://lh3.googleusercontent.com/d/"));
        i=i+1;
    })})
    setSelectedData(imgs);
    setTotal(i);
    console.log(i);
  }, []);

  const playPrev = () => {
    if(selectedImage>0){
      setSelectedImage(selectedImage-1);
      setSelectedDataChange((prev) => !prev);
    }
  };
  
  const playNext = () => {
    if(selectedImage<total-1){
      setSelectedImage(selectedImage+1);
      setSelectedDataChange((prev) => !prev);
    }
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setIsCert(false);
  };
  const handleClickDownload = (event) => {
    window.open(event,'_blank');
}
  return (
    <div>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">ผลงานและเกียรติบัตร</DialogTitle>
        <DialogContent>
        
          {selectedData && 
                ( selectedDataChange ? (
                  <div style={{ height: "100%"}}>
            <a name="top"></a>

  <div style={{ height: "100%", display: "flex", paddingTop: "20px", flexDirection: "column", justifyContent: "center"}}>
<LazyLoadImage alt={selectedData[selectedImage]} effect="blur" width="100%"
                src={selectedData[selectedImage]} onClick={()=>{handleClickDownload(selectedData[selectedImage])}}/> </div>
</div>
                ):
                (
                  <div style={{ height: "100%"}}>
            <a name="top"></a>

                  <div style={{ height: "100%", display: "flex", paddingTop: "20px", flexDirection: "column", justifyContent: "center"}}>
                <LazyLoadImage alt={selectedData[selectedImage]} effect="blur" width="100%"
                                src={selectedData[selectedImage]} onClick={()=>{handleClickDownload(selectedData[selectedImage])}}/> </div>
                
               </div>
                ))}
          <Hidden xsDown>
        <GridList cellHeight={130} className={classes.gridList} cols={3}>
        {data.map((tile,index) => (
          <GridListTile key={index} cols={tile.cols || 1} 
          onClick={()=>{handleClickDownload(tile.pictures.split(',')[0])}}>
            <img key={tile.pictures.split(',')[0]} alt={tile.title}
            src={tile.pictures.split(',')[0].trim().replace("https://drive.google.com/open?id=", 
            "https://lh3.googleusercontent.com/d/").concat("=w600")}/> 
          </GridListTile>
        ))}
      </GridList>
      </Hidden>
      <Hidden smUp>
        <GridList cellHeight={140} className={classes.gridList} cols={2}>
        {data.map((tile,index) => (
          <GridListTile key={index} cols={tile.cols || 1} 
          onClick={()=>{handleClickDownload(tile.pictures.split(',')[0])}}>
            <img key={tile.pictures.split(',')[0]} alt={tile.title}
            src={tile.pictures.split(',')[0].trim().replace("https://drive.google.com/open?id=", 
            "https://lh3.googleusercontent.com/d/").concat("=w600")}/> 
          </GridListTile>
        ))}
      </GridList>
      </Hidden>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" style={{position: "absolute", top: 1, right: 1}}>
            ปิด
          </Button>
          <Button aria-label="delete" onClick={playPrev}
                className={classes.margin} style={{position: "absolute", left: 0, bottom: 8, display: "flex", flexDirection: "column", justifyContent: "center", backgroundImage:
                "linear-gradient( 270deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)",}}>
                    <a href="#top">{"<"}</a>
                </Button>
                <Button aria-label="delete"  onClick={playNext}
                className={classes.margin} style={{ position: "absolute", right: 8, bottom: 8,  display: "flex", flexDirection: "column", justifyContent: "center",backgroundImage:
                "linear-gradient( 90deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)",}}>
                    <a href="#top">{">"}</a>
                </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}