import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import OrderPrint from './OrderPrint';
import TeachingPrint from './TeachingPrint';
import PrintIcon from '@material-ui/icons/Print';

export default function ExportDialog({data, category, index}) {
  const [open, setOpen] = React.useState(false);
  const [url, setURL] = React.useState("");

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleURL = (event) => {
    setURL(event);
  };
  const download = () => {
    window.location.assign(url);
  };

  return (
    <div>
      <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        <PrintIcon></PrintIcon> PDF
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"กดปุ่มด้านล่างเพื่อดาวน์โหลด"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
              {index===0 && (
                <OrderPrint data={data} category={category}></OrderPrint>
              )}
              {index===1 && (
                <TeachingPrint data={data} category={category}></TeachingPrint>
              )}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" autoFocus>
            ยกเลิก
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}